/* App.css */

.intro-animation {
  /* ... existing styles */
}

.App {
  text-align: center;
}

nav {
  padding-top: 30px;
  padding-bottom: 50px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  background-color: #151719;
}

nav .margint ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  text-decoration: none;
}

.margint ul {
  display: flex;
}

.pages {
  position: relative;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  color: #a0a0a0;
  font-size: 25px;
  letter-spacing: 0.5px;
  padding: 0 10px;
}

/* Hide Menu Links and show Hamburger icon on smaller screens */
.hamburger {
  display: none;
  font-size: 30px;
  cursor: pointer;
  color: white;
}

.show-menu ul {
  display: block;
}

/* Responsive Styles */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    align-items: center;
  }

  /* Hide the nav links and show the hamburger on mobile */
  .margint ul {
    display: none;
    flex-direction: column;
    text-align: center;
  }

  .show-menu ul {
    display: flex;
  }

  .hamburger {
    display: block;
  }
}

/* Waviy animation styles */
.waviy {
  font-size: 30px;
}

.waviy span {
  font-family: 'Alfa Slab One', cursive;
  color: #fff;
  animation: waviy 8s ease-in-out infinite;
  animation-delay: calc(.1s * var(--i));
}

@keyframes waviy {
  0%, 12.5%, 100% {
    transform: translateY(0);
  }
  6.25% {
    transform: translateY(-20px);
  }
}
